<template>
	<div>
		<listItem
			v-for="(item,index) in itemData"
			:key="index"
			:brname="item.name"
			:brauthor="item.author"
		>
			<img src="../../../../static/img/broadcaster_list_item.png" slot="broadcaster-list-item-img-slot">
		</listItem>
	</div>
</template>

<script>
	import listItem from './broadcaster-list-item'

	export default{
		components:{
			listItem
		},
		data(){
			return {
				itemData: [
					{
						name: '',
						author: ''
					},
					{
						name: 'FM',
						author: 'FM'
					},
					{
						name: 'NJNJ',
						author: ''
					},
					{
						name: '',
						author: ''
					},
					{
						name: 'STUDIO',
						author: '-'
					},
					{
						name: '',
						author: ''
					},
					{
						name: 'S.N.',
						author: ''
					},
					{
						name: '',
						author: '--'
					},
					{
						name: '',
						author: ''
					},
					{
						name: '',
						author: ''
					}
				]
			}
		}
	}
</script>


